.app-header {
    background-color: transparent;
}

.main-app-header {
    border-bottom: 1px solid #e4e5e7;
}

.user-link {
    color: $clr5;
    background-color: transparent;
    transition: color .5s ease, border .5s ease;

    &:hover {
        color: $clr1;
    }
}

.header-home-page {
    position: absolute;
    z-index: 2;
    width: 100%;

    & * {
        color: $clr3;
    }
}

.logo {
    font-size: rem(32px);
    font-family: macan-bold;
    margin-right: em(32px, 27px);
    color: $clr2;

    span {
        color: $clr1;
    }

    &:hover {
        color: $clr2;
    }
}

.home-page-link {
    color: $clr3;

    &:hover {
        color: $clr3;
    }
}

.debug {
    border: 1px solid red;
}

.sticky {
    z-index: 20;
    position: sticky;
    background-color: $clr3;
    top: 0;

    & * {
        color: $clr5;
    }
    transition: all 0.5s;
}

.no-display {
    display: none;
}

.grid-full {
    grid-column: 1/-1;
}